import React, { useState } from 'react'
import IngresoDatos from './IngresoDatos';
import MiLecheria from "../Media/MiLecheria.jpg"
import LogoSaltoAgro from "../Media/LogoSaltoAgro.png"
import LogoML1 from "../Media/LogoML1.png"

function Presentacion() {

    const [mostrarInstrucciones, setMostrarInstrucciones] = useState(true);
    const [comenzar, setComenzar] = useState(false);
    const [mostrarGeneralidades, setMostrarGeneralidades] = useState(false);
    const [mostrarResultados, setMostrarResultados] = useState(false);
    const [mostrarSecuencia, setMostrarSecuencia] = useState(false);
    const [mostrarTerminos, setMostrarTerminos] = useState(false);


    const manejarOnClick1 = () => {
        setMostrarInstrucciones(!mostrarInstrucciones);
    }

    const manejarOnclick2 = () => {
        setComenzar(true);
    }

    const manejarOnClick3 = () => {
        setMostrarGeneralidades(!mostrarGeneralidades);
    }

    const manejarOnClick4 = () => {
        setMostrarResultados(!mostrarResultados);
    }

    const manejarOnClick5 = () => {
        setMostrarSecuencia(!mostrarSecuencia);
    }

    const manejarOnClick6 = () => {
        setMostrarTerminos(!mostrarTerminos);
    }

    return (
        <div>
            <header className="App-header">
                <img src={MiLecheria} id="MiLecheria"></img>
                <br></br>
                <h1>CRECIMIENTO DEL RODEO LECHERO</h1>
            </header>
            {mostrarInstrucciones && (<div className="seccion">
                <h2>Introduccin:</h2>
                <div id="instrucciones">
                    <button onClick={manejarOnClick3} className='mostrar'>
                        {mostrarGeneralidades === true ? "Generalidades y objetivos  " : "Generalidades y objetivos  "}
                    </button>
                    {mostrarGeneralidades && (<div>
                        <p>Durante un ejercio anual un rodeo lechero crece cuando las vacas adultas que finalizan su vida til (muerte y/o rechazo) son menos que las vacas nuevas (vaquillonas de reemplazo) que inician su vida productiva, finalizando el perodo con ms vacas adultas que al comienzo.
                            Esto debera lograrse como resultado de la dinmica propia del rodeo (crecimiento genuino) sin recurrir a compra de vacas y/o vaquillonas.</p>
                        <p>Objetivos de esta aplicacin:</p>
                        <ul>
                            <li>Despertar inters por la enorme oportunidad que supone el crecimiento del rodeo lechero</li>
                            <li>Contribuir a la comprensin de los parmetros/procesos relacionados</li>
                            <li>Ofrecer una herramienta de cuantificacin para uso didctico y aplicacin prctica</li>
                        </ul>
                        <p>Mediante el uso de esta aplicacin web se puede simular el crecimiento genuino de un rodeo lechero ingresando ciertos parmetros del rodeo.</p>
                        <p>El ingreso de parmetros est dividido en dos formularios: "Clculo de bajas anuales y vida til de vacas adultas" y "Clculo de reposicin anual efectiva". Debe completarse el primero para acceder al segundo.
                            Para cada input puede consultarse una definicin simplificada o un breve mensaje de ayuda. Los valores ingresados pueden cambiarse en cualquier momento.</p>
                    </div>)}
                    <br></br>
                    <button onClick={manejarOnClick4} className='mostrar'>
                        {mostrarResultados === true ? "Resultados que se obtienen  " : "Resultados que se obtienen  "}
                    </button>
                    {mostrarResultados && (<div>
                        <ul>
                            <li>Porcentaje anual de bajas</li>
                            <li>Duracin media estimada de las vacas</li>
                            <li>Porcentaje anual de reposicin efectiva de corto y largo plazo</li>
                            <li>Grfico comparando bajas con reposicin de corto y largo plazo</li>
                            <li>Relacin Reemplazos/Bajas (corto y largo plazo)</li>
                            <li>Crecimiento porcentual anual del rodeo (corto y largo plazo)</li>
                            <li>Grfico de evolucin comparada a cinco aos (para tres estados diferentes del rodeo y partiendo del nmero de vacas elegido por el usuario)</li>
                        </ul>
                        <p>En este ltimo grfico los tres estados toman por defecto los valores de la primera simulacin terminada. Luego de hacer los cambios deseados cualquier situacin simulada puede guardarse en un estado y visualizar su curva de evolucin.</p>
                        <p>Se podr generar un reporte en formato PDF ingresando datos adicionales incluido un email vlido al que ser enviado dicho reporte.</p>
                    </div>)}
                    <br></br>
                    <button onClick={manejarOnClick5} className='mostrar'>
                        {mostrarSecuencia === true ? "Secuencia de trabajo sugerida  " : "Secuencia de trabajo sugerida  "}
                    </button>
                    {mostrarSecuencia && (<div>
                        <ol>
                            <li>Diagnstico (realidad actual) (Estado 1)</li>
                            <li>Deteccin de puntos crticos</li>
                            <li>Qu pasara si...? (pruebas)</li>
                            <li>Seleccin de dos alternativas exitosas (Estados 2 y 3)</li>
                            <li>Emisin de reporte final</li>
                        </ol>
                    </div>)}
                    <br></br>
                    <button onClick={manejarOnClick6} className='mostrar'>
                        {mostrarTerminos === true ? "Trminos de uso  " : "Trminos de uso  "}
                    </button>
                    {mostrarTerminos && (<div>
                        <p>Debido a la gran cantidad de variables involucradas en la seleccin de datos de entrada, la interpretacin de resultados y las aplicaciones de esta herramienta en general, el usuario asume toda la responsabilidad por su uso.</p>
                    </div>)}

                </div>
            </div>)}
            <div>
                <button onClick={manejarOnClick1}>
                    {mostrarInstrucciones === true ? "Ocultar introduccin" : "Mostrar introduccin"}
                </button>
                {comenzar === false && (<button onClick={manejarOnclick2}>
                    Aceptar trminos y comenzar
                </button>)}
            </div>
            {comenzar && (<div>
                <IngresoDatos />
            </div>)}
            <footer className='footer'>
                <div>
                    <p><b>Desarrollador:</b></p>
                    <p>Ing. Agr. EPL Francisco Candioti</p>
                    <p>panchocandioti@gmail.com</p>
                </div>
                <div>
                    <p><b>Con el apoyo de:</b></p>
                </div>
                <div className='containerLogos'>
                    <img src={LogoSaltoAgro} className='logo'></img>
                    <img src={LogoML1} className='logo'></img>
                </div>
            </footer>
        </div>
    )
}

export default Presentacion